ul {
  margin: 0;
  padding: 0;
}

.wrapper {
  width: 480px;
  height: 600px;
  background-color: rgba(0, 0, 0, .9);
  outline: 1px solid #000;
  padding: 12px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.input {
  width: 462px;
  height: 72px;
  border: 2px solid #000;
  padding-left: 12px;
  font-size: 48px;
}

.numbers_list {
  flex-wrap: wrap;
  justify-content: center;
  gap: 12px;
  margin-top: 24px;
  list-style: none;
  display: flex;
}

.item_button {
  width: 88px;
  height: 88px;
  color: #f0f8ff;
  background-color: rgba(0, 0, 0, .1);
  outline: 1px solid #000;
  justify-content: center;
  align-items: center;
  font-size: 64px;
  display: flex;
}

.item_button:hover {
  cursor: pointer;
}

.item_button:active {
  color: #000;
  background-color: #fff;
}

.on, .off, .ac {
  font-size: 42px;
}

.span {
  height: 50%;
}

